<template>
  <div class="body" :style="{ zoom: zoomwh }">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      zoomwh: '100%'
    }
  },
  mounted() {
    // 更新缩放
    this.getWindowSzie()
  },
  methods: {
    /**
     * 获取窗口大小
     */
    getWindowSzie() {
      const h = screen.height
      const w = screen.width

      if (h < 1080 && w < 1920) {
        this.zoomwh = '75%'
      }
    }
  }
}
</script>
<style scoped>
@import '../src/assets/fonts/font.css';
.body {
  font-family: 'FiraCode-Retina';
}
</style>
