import { createGUID } from '../../scripts/internal'
import { nowdate } from '../../scripts/common'
export default {
  state: {
    /**
     * 活动的释放记录
     */
    activeRelease: {},

    /**
     * 历史释放记录
     */
    historyRelease: []
  },
  mutations: {
    /**
     * 初始化活动的释放记录任务
     * @param {*} state
     * @param {*} payload
     */
    initActiveRelease(state, payload) {
      state.activeRelease = {} // 清空原值
      if (payload != null) {
        // 更新其他的
        state.activeRelease = {
          taskid: createGUID(),
          createdate: nowdate(),
          modifydate: nowdate(),
          assigner: '',
          createautor: '',
          modifyautor: '',
          counter: 0,
          case: [],
          us: [],
          devicelist: [],
          scriptlist: [],
          configlist: [],
          reviewautor: '',
          reviewstatus: '',
          taskname: '',
          tasktype: 'RELEASE',
          taskstatus: 'active',
          isoffline: true
        }

        if (payload.name !== null && payload.name !== undefined && payload.name !== '') {
          state.activeRelease.taskname = payload.name
        } else {
          state.activeRelease.taskname = 'TASK-' + createGUID()
        }
        if (payload.assigner !== null && payload.assigner !== undefined && payload.assigner !== '') {
          state.activeRelease.assigner = payload.assigner
        } else {
          state.activeRelease.assigner = '无名氏'
        }
        if (payload.createautor !== null && payload.createautor !== undefined && payload.createautor !== '') {
          state.activeRelease.createautor = payload.createautor
          state.activeRelease.modifyautor = payload.createautor
        } else {
          state.activeRelease.createautor = '无名氏'
          state.activeRelease.modifyautor = '无名氏'
        }
        state.activeRelease.basic = {
          product: '',
          type: '',
          relversion: '',
          uppack: false,
          configsvn: '',
          expectdate: '',
          acutaldate: '',
          relphase: '' // 产品本年度第几次释放
        }
        state.activeRelease.trcase = []
        state.activeRelease.email = {
          email: '',
          origin: [],
          dest: []
        }
        state.activeRelease.test = {
          version: false,
          versionlist: ['', '', ''],
          new: false,
          improve: false,
          fix: false,
          cod: false,
          updatepackage: false,
          license: false,
          regressiontest: false
        }
        state.activeRelease.doc = {
          certificate: '',
          swlistpath: '',
          whatsnewpath: '',
          targetreleasepath: '',
          devlopreleasemail: false,
          testplan: false,
          releaseplan: false,
          use360: false,
          automatic: false
        }
        console.log('初始化Active释放任务对象成功')
      }
    },
    /**
     * 更新活动的释放记录任务
     * @param {*} state
     * @param {*} payload
     */
    updateActiveRelease(state, payload) {
      if (payload != null) {
        if (payload.type === 'basic') {
          state.activeRelease.basic = payload.value
        }
        if (payload.type === 'trcase') {
          state.activeRelease.trcase = payload.value
        }
        if (payload.type === 'email') {
          state.activeRelease.email.email = payload.value
        }
        if (payload.type === 'origin') {
          state.activeRelease.email.origin = payload.value
        }
        if (payload.type === 'dest') {
          state.activeRelease.email.dest = payload.value
        }
        if (payload.type === 'test') {
          state.activeRelease.test = payload.value
        }
        if (payload.type === 'doc') {
          state.activeRelease.doc = payload.value
        }
        console.log('更新Active释放任务对象成功')
      }
    },
    /**
     * 更新历史释放记录
     * @param {*} state 状态
     * @param {*} history 历史记录对象
     */
    uploadHistoryRelease(state, payload) {
      if (payload != null) {
        state.historyRelease.push(payload)
        console.log('uploadHistoryRelease提交成功')
      }
    },
    /**
     * 更新历史释放记录
     * @param {*} state 状态
     * @param {*} history 历史记录对象
     */
    uploadHistoryReleaseByFile(state, payload) {
      if (payload != null) {
        const json = JSON.parse(payload)
        state.historyRelease = json
        console.log('uploadHistoryRelease提交成功')
      }
    },
    takeHistoryIntoActive(state, payload) {
      if (payload != null) {
        const source = state.historyRelease.splice(payload.index, 1)
        state.activeRelease = source[0]
        console.log(state.activeRelease)
        console.log('takeHistoryIntoActive执行成功')
      }
    }
  },
  actions: {}
}
