/**
 * COM: tasktile.vue
 * NOTICE: 应该从后端返回对应人的所有任务
 */
const totaltask = () => {
  const temptotal = []
  // 组装数据
  const status = [TASKSTATUS_EN.COMPLETED, TASKSTATUS_EN.EXEC, TASKSTATUS_EN.HOLD, TASKSTATUS_EN.BLOCK]
  for (let index = 0; index < 25; index++) {
    const sar = Math.floor(Math.random() * status.length) // 随机状态
    const element = {
      id: index,
      createdate: nowdate(),
      modifydate: nowdate(),
      assigner: '居溱',
      createautor: '冯涛',
      modifyautor: '刘凯',
      counter: 44,
      case: ['TTP 1111', 'TTP 2222', 'TTP 3333', 'TTP 4444'],
      us: ['US-0001 测试', 'US-0002 测试', 'US-0003 测试', 'US-0004 测试'],
      devicelist: [],
      scriptlist: ['test1.ts', 'test2.js', 'test3.xml'],
      configlist: ['archonetestttol.json'],
      reviewautor: '李荧辉',
      reviewstatus: 'need review',
      taskid: createGUID(),
      taskname: `Task0001_MTStar_US00` + index + `自动化测试`,
      tasktype: 'Develop',
      taskstatus: status[sar],
      isoffline: true
    }
    temptotal.push(element)
  }
  return temptotal
}

/**
 * 当前时间
 * NOTICE: yyyy-mm-dd HH:MM:ss:l(yyyy-mm-dd HH:MM:ss.SSS)
 */
const nowdate = () => {
  const now = new Date()
  const tnd = dateFormat(now, 'yyyy-mm-dd HH:MM:ss.l')
  return tnd
}

/**
 * 执行日志
 * UPDATE: 2021-02-10
 */
const execloglist = () => {
  const tempexeclog = []
  // 组装数据
  const status = [TASKSTATUS_EN.COMPLETED, TASKSTATUS_EN.EXEC, TASKSTATUS_EN.HOLD, TASKSTATUS_EN.BLOCK]
  const types = ['Get', 'Post', 'APIs']
  for (let index = 0; index < 6; index++) {
    const sar = Math.floor(Math.random() * status.length) // 随机状态
    const tar = Math.floor(Math.random() * types.length) // 随机状态
    const element = {
      id: index,
      execdata: nowdate(),
      exectype: types[tar],
      taskstatus: status[sar],
      extrainfo: '执行完毕,返回200OK'
    }
    tempexeclog.push(element)
  }
  return tempexeclog
}

/**
 * 获取所有的项目
 * COM: settings.vue
 * STATUS: 2021-05-19 完成修改
 */
const allprojects = () => {
  const defaultvalue = [
    {
      to: 'scalemanager',
      name: 'TEST-SCALEMANAGER(项目)'
    },
    {
      to: 'mtstar',
      name: 'TEST-MTSTAR(项目)'
    },
    {
      to: 'archonepc',
      name: 'TEST-ARCHONE-PC(项目)'
    },
    {
      to: 'pcsoft',
      name: 'TEST-PCSOFT(项目)'
    },
    {
      to: 'archone',
      name: 'TEST-ARCHONE(项目)'
    }
  ]
  // 含有后台的时候，可以直接从vuex内获取部分信息
  const user = store.state.common.activeusers
  // 从远程获取,取数据
  return new Promise(function(resolve, reject) {
    getFunc('/query/allprojects', {
      userid: user.id
    }).then(res => {
      if (res.resstatus === true) {
        const result = JSON.parse(res.resbody) // 转义为json格式数据
        resolve(result)
      } else {
        reject(defaultvalue)
      }
    })
  })
}

/**
 * 项目轮播展示
 * @param {*} porjectname 项目名称
 */
const getCarousels = porjectname => {
  const tempcarousels = []
  // 组装数据
  const moorigins = makeEemu(MESSAGEORIGIN_CN)
  const mtypes = makeEemu(MESSAGETYPE_CN)
  for (let index = 0; index < 20; index++) {
    const tmorigin = Math.floor(Math.random() * moorigins.length) // 随机状态
    const tmtype = Math.floor(Math.random() * mtypes.length) // 随机状态

    const carousel = {
      projectname: porjectname,
      messageorigin: moorigins[tmorigin], // 消息来源
      messagetype: mtypes[tmtype], // 消息类型
      datetime: nowdate(), // 发送消息时间
      messagetitle: 'DEBUG测试尝试abcd' + index,
      messagecontent: 'Bulma is a free, open source'
    }
    tempcarousels.push(carousel)
  }
  return tempcarousels
}

/**
 * 根据项目名称获取前5天的状态任务
 * 某某天，TASKSTATUS_EN(CN)多少
 * @param {*} porjectname
 */
const getPorjectStatusByDate = porjectname => {
  const tempdate = []
  // 组装数据
  const tsen = makeEemu(TASKSTATUS_EN)
  tsen.splice(0, 1) // 删除第一个
  tsen.forEach(element => {
    const value = []
    for (let index = 0; index < 7; index++) {
      let tempvalue
      if (element === TASKSTATUS_EN.TOTAL) {
        tempvalue = Math.ceil(Math.random() * 10 + 3) + 100 // 数值
      } else {
        tempvalue = Math.ceil(Math.random() * 10 + 3) // 数值
      }
      value.push({
        date: index, // 日期
        value: tempvalue
      })
    }
    const date = {
      type: element,
      values: value
    }
    tempdate.push(date)
  })

  return tempdate
}

