/**
 * 通用
 */
export default {
  state: {
    /**
     * 保存所有的testrun
     * 无结果
     */
    testrun: [],
    /**
     * 保存所有的testrun
     * 含执行结果
     */
    testrunAll: []
  },
  mutations: {
    /**
     *  更新
     * @param {*}} state 状态
     * @param {*} payload TestRun载入记录
     */
    uploadTestRun(state, payload) {
      if (payload != null) {
        const index = state.testrun.findIndex(element => element.id === payload.id)
        if (index !== -1) {
          state.testrun[index] = payload
          console.log('TestRun 升级成功')
        } else {
          state.testrun.push(payload)
          console.log('TestRun 添加成功')
        }
      }
    },

    /**
     *  更新
     * @param {*}} state 状态
     * @param {*} payload TestRun载入记录
     */
    uploadTestRunAll(state, payload) {
      if (payload != null) {
        const index = state.testrunall.findIndex(element => element.id === payload.id)
        if (index !== -1) {
          state.testrunall[index] = payload
          console.log('TestRunAll 升级成功')
        } else {
          state.testrunall.push(payload)
          console.log('TestRunAll 添加成功')
        }
      }
    },

    /**
     *
     * @param {*} state
     * @param {*} payload
     */
    importTestRunCommon(state, payload) {
      if (payload != null) {
        const json = JSON.parse(payload)
        state.testrun = json
        console.log('导入testrun成功')
      }
    }
  },
  actions: {}
}
