import { getAllMessage } from '../../scripts/common'
/**
 * 通用
 */
export default {
  state: {
    /**
     * UPDATE: 不再通过cookie进行用户记录
     * NOTICE: 活动用户列表，不通过cookie进行管理，通过activeusers.id进行更新和状态更新
     */
    activeusers: {
      id: '',
      unickname: '',
      uemail: '',
      uname: '',
      upsd: '',
      ulastlogin: ''
    },
    /**
     * COM: message.vue/sidemessage.vue
     * UPDATE: 2021-02-23
     * NOTICE: 所有的站内信信息
     * NOTICE: 等下一波更新
     */
    allmessages: getAllMessage()
  },
  mutations: {
    /**
     * 更新用户信息
     * @param {*} state
     * @param {*} payload
     */
    updateUserInfo(state, payload) {
      // console.log(payload)
      if (payload != null) {
        if (state.activeusers.id === payload.resbody.id) {
          // 存在，则更新
          state.activeusers = payload.resbody
          console.log('Common 更新用户成功')
        } else {
          // 不存在，则加入到最后
          state.activeusers = payload.resbody
          console.log('Common 添加用户成功')
        }
      }
      // console.log(state.activeusers)
    }
  },
  actions: {}
}
