import releasemanagement from './Func/releasemanagement'
import testrun from './Func/testrun'
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    testsuite: testsuite,
    common: common,
    twoproject: twoproject,
    releasemanagement: releasemanagement,
    testrun: testrun
  }
  // strict: true
})
