/**
 * https://www.cnblogs.com/hello9102/p/13440518.html
 */

import Vue from 'vue'
import VueRouter from 'vue-router'

// layout
const Layout = () => import('@/views/layout/Layout')

// 仪表盘
const Home = () => import('@/views/Home')

// 图表
const Charts = () => import('@/views/charts/Charts')

// profile
const TaskList = () => import('@/views/profile/TaskList')
const Settings = () => import('@/views/profile/Settings')
/*
  * UPDATE: 2021-02-23
  * NOTICE: 站内信现在移到了TOPNAV上
const Message = () => import('@/views/profile/Message')
*/

// pages
const Login = () => import('@/views/pages/Login')
const Register = () => import('@/views/pages/Register')

/**
 * UPDATE: 2021-02-23
 * NOTICE: 后续项目应该只有默认第一个和默认第二展示
 */

// project
const FirstProject = () => import('@/views/project/FirstProject')
const SecondProject = () => import('@/views/project/SecondProject')

// craft
const Interface = () => import('@/views/craft/Interface')
const InterfaceRun = () => import('@/views/craft/InterfaceRun')

// release
const NowSprint = () => import('@/views/release/NowSprint')
const History = () => import('@/views/release/History')

// doc
const Course = () => import('@/views/docs/Course')
const Apis = () => import('@/views/docs/Apis')

// 关于
const About = () => import('@/views/docs/About')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/dashboard', // 主节点(仪表盘)
    // name: 'main',
    component: Layout,
    meta: {
      // label: '主页'
    },
    children: [
      {
        path: 'dashboard',
        name: 'dashboard',
        component: Home,
        meta: {
          label: '仪表盘'
          // requireAuth: true
        }
      },
      {
        path: 'profile',
        // redirect: '/profile/tasklist',
        // name: '个人信息',
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [
          /*   UPDATE: 2021-02-23 不要了
        {
            path: 'message',
            name: 'message',
            component: Message,
            meta: {
              label: '站内信'
              // requireAuth: true
            }
          },
          */
          {
            path: 'tasklist',
            name: 'tasklist',
            component: TaskList,
            meta: {
              label: '任务列'
              // requireAuth: true
            }
          },
          {
            path: 'settings',
            name: 'settings',
            component: Settings,
            meta: {
              label: '个人设置'
              // requireAuth: true
            }
          }
        ]
      },
      {
        path: 'release',
        // redirect: '/release/nowsprint',
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [
          {
            path: 'nowsprint',
            name: 'nowsprint',
            component: NowSprint,
            meta: {
              label: '当前释放版本'
              // requireAuth: true
            }
          },
          {
            path: 'history',
            name: 'history',
            component: History,
            meta: {
              label: '历史释放'
              // requireAuth: true
            }
          }
        ]
      },
      {
        path: 'project',
        // redirect: '/proejct/firstproject',
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [
          {
            path: 'first',
            name: 'first',
            component: FirstProject,
            meta: {
              label: '预展示第一项目'
              // requireAuth: true
            }
          },
          {
            path: 'second',
            name: 'second',
            component: SecondProject,
            meta: {
              label: '预展示第二项目'
              // requireAuth: true
            }
          }
        ]
      },
      {
        path: 'craft',

        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [
          {
            path: 'interface',
            name: 'interface',
            component: Interface,
            meta: {
              label: '接口测试(Web)'
              // requireAuth: true
            }
          },
          {
            path: 'interfacerun',
            name: 'interfacerun',
            component: InterfaceRun,
            meta: {
              label: '接口测试(Web)执行'
              // requireAuth: true
            }
          }
        ]
      },
      {
        path: 'docs',
        // redirect: '/docs/course',
        component: {
          render(c) {
            return c('router-view')
          }
        },
        children: [
          {
            path: 'course',
            name: 'course',
            component: Course,
            meta: {
              label: 'Course'
              // requireAuth: true
            }
          },
          {
            path: 'apis',
            name: 'apis',
            component: Apis,
            meta: {
              label: 'Apis'
              // requireAuth: true
            }
          },
          {
            path: 'about',
            name: 'about',
            component: About,
            meta: {
              label: 'About'
            }
          }
        ]
      },
      {
        path: 'charts',
        name: 'charts',
        component: Charts,
        meta: {
          // label: '报表'
        }
      }
    ]
  },
  {
    path: '/',
    component: {
      render(c) {
        return c('router-view')
      }
    },
    children: [
      {
        path: 'login',
        name: 'login',
        component: Login,
        meta: {
          label: '登陆'
        }
      },
      {
        path: 'register',
        name: 'register',
        component: Register,
        meta: {
          label: '注册'
        }
      }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

export default router
